.cart-item {
    width: 100%;
    display: flex;
    height: 80px;
    margin-bottom: 15px;
  
    img {
      width: 30%;
    }
  
    .item-details {
      width: 70%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 10px 15px;
  
      .name {
        font-size: 16px;
        margin-top: 3px;
      }

      .price {
        display: flex;
        justify-content: space-between;
        width: 80%;
        
      }
    }
  }